import logo from "./logo.png";
import linkedin from "./Assets/CompanyLogos/linkedin.svg";
import "./Navbar.css";
import { Link } from "react-router-dom";
import React, { useState } from "react";
import { Squash as Hamburger } from "hamburger-react";

export default function Navbar() {
  const [openMenu, setOpenMenu] = useState(false);
  const toggleMenu = () => {
    setOpenMenu(!openMenu);
  };

  return (
    <nav className="Nav">
      <ul className="navigation">
        <li className="mobileFormat">
          <div className="hamburgerMenu">
            <Hamburger
              toggled={openMenu}
              toggle={setOpenMenu}
              color="white"
            ></Hamburger>
          </div>
          <Link to="/" className="logolinkMobile">
            <img className="logo" src={logo} alt="Platform Logo" />
          </Link>
          <div className="invisible"></div>
        </li>
        {openMenu ? (
          <>
            <li className="navLinksMobile">
              <Link to="/" onClick={toggleMenu}>
                Home
              </Link>
            </li>
            <li className="navLinksMobile">
              <Link to="PlatformLaunchPartners" onClick={toggleMenu}>
                Platform Launch Partners
              </Link>
            </li>
            <li className="navLinksMobile">
              <Link to="About" onClick={toggleMenu}>
                About
              </Link>
            </li>
            <li className="navLinksMobile">
              <Link to="InThePress" onClick={toggleMenu}>
                In The Press
              </Link>
            </li>
            <li className="navLinksMobile">
              <Link to="JoinTheInitiative" onClick={toggleMenu}>
                Join The Initiative
              </Link>
            </li>
            <li className="navLinksMobile">
              <a
                style={{ padding: 10 }}
                href="https://www.linkedin.com/company/ethereum-climate-alliance"
                target="_blank"
              >
                <img
                  style={{ height: 30 }}
                  src={linkedin}
                  alt="Platform Logo"
                />
              </a>
            </li>
          </>
        ) : null}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            width: "100%",
          }}
        >
          <div style={{ display: "flex" }}>
            <Link to="/" className="logolink">
              <img className="logo" src={logo} alt="Platform Logo" />
            </Link>
            <div className="navLinksDesktop">
              <Link to="/">Home</Link>
            </div>
            <div className="navLinksDesktop">
              <Link to="PlatformLaunchPartners">Platform Launch Partners</Link>
            </div>
            <div className="navLinksDesktop">
              <Link to="About">About</Link>
            </div>
            <div className="navLinksDesktop">
              <Link to="InThePress">In The Press</Link>
            </div>
            <div className="navLinksDesktop">
              <Link to="JoinTheInitiative">Join The Initiative</Link>
            </div>
          </div>
          <div className="navLinksDesktop">
            <a
              className="linkedInLogo"
              style={{ padding: 10 }}
              href="https://www.linkedin.com/company/ethereum-climate-alliance"
              target="_blank"
            >
              <img style={{ height: 30 }} src={linkedin} alt="Platform Logo" />
            </a>
          </div>
        </div>
      </ul>
    </nav>
  );
}
