import "./About.css";

import React from "react";
import AboutBanner from "../Assets/AboutBanner.webp";
import AboutBannerMobile from "../Assets/AboutBannerMobile.webp";
import ClimateGraphics from "../Assets/ClimateGraphics.png";
import EthereumMergeMobile from "../Assets/EthereumMergeMobile.png";
import EthereumMergeMobileGraphic from "../Assets/EthereumMergeMobileGraphic.png";
import EthereumClimateMobile from "../Assets/EthereumClimateMobile.png";
import EthereumClimateMobileGraphic from "../Assets/EthereumClimateMobileGraphic.png";
import { Helmet } from "react-helmet";

const About = () => (
  <>
    <Helmet>
      <title>About | Ethereum Climate Platform</title>
      <meta
        name="description"
        content="ECP is an initiative designed to collaboratively build a blockchain-based technology platform that will engage a decentralized community to accelerate climate finance at scale."
      />
    </Helmet>
    <img src={AboutBanner} alt="bannerImage" className="AboutBanner"></img>
    <img
      src={AboutBannerMobile}
      alt="bannerImageMobile"
      className="AboutBannerMobile"
    ></img>
    <div className="PageBody">
      <img
        src={ClimateGraphics}
        alt="Climate Graphics"
        className="ClimateGraphics"
      ></img>
      <img
        src={EthereumMergeMobile}
        alt="Ethereum Merge Mobile"
        className="EthereumMergeMobile"
      ></img>
      <img
        src={EthereumMergeMobileGraphic}
        alt="Ethereum Merge Mobile Graphic"
        className="EthereumMergeMobileGraphic"
      ></img>
      <img
        src={EthereumClimateMobile}
        alt="Ethereum Climate Mobile"
        className="EthereumClimateMobile"
      ></img>
      <img
        src={EthereumClimateMobileGraphic}
        alt="Ethereum Climate Mobile Graphic"
        className="EthereumClimateMobileGraphic"
      ></img>
      <h3 className="Web3Blurb">
        As a Web3-enabled platform employing decentralized mechanisms and
        incentives to engage a climate-aligned community, the ECP will implement
        a formulaic, automated screening, and capital deployment process,
        optimizing speed, transparency, and objectivity to maximize climate
        impact.
      </h3>
    </div>
  </>
);

export default About;
