import React from "react";
import { Link } from "react-router-dom";
import "./Home.css";
import HeroBanner from "../Assets/HeroBanner.webp";
import HeroBannerMobile from "../Assets/HeroBannerMobile.webp";
import TrustedBy from "../Assets/TrustedBy.svg";
import TrustedByMobile from "../Assets/TrustedByMobile.svg";
import Union from "../Assets/Union.svg";
import Hands from "../Assets/Hands.png";
import PressRoundup from "../Assets/PressRoundup.png";
import { Helmet } from "react-helmet";

const Home = () => (
  <>
    <Helmet>
      <title>Ethereum Climate Platform</title>
      <meta
        name="description"
        content="Leveraging blockchain technology to scale climate investments."
      />
    </Helmet>
    <img src={HeroBanner} alt="bannerImage" className="HeroBanner"></img>
    <img
      src={HeroBannerMobile}
      alt="bannerImageMobile"
      className="HeroBannerMobile"
    ></img>
    <div className="PageBody">
      <iframe
        width="560"
        height="315"
        src="https://www.youtube-nocookie.com/embed/SISoGPoJYAI"
        title="YouTube video player"
        className="YoutubeUNFCCC"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
      ></iframe>
      <h2 className="VideoSubtext">
        Check out our launch announcement at the The United Nations Framework
        Convention on Climate Change's recent Conferences of the Parties
        (COP27).
      </h2>
      <button className="VideoButton">
        <a href="https://youtu.be/-4zIFS7EXV0">Full Video</a>
      </button>
    </div>
    <div className="PageBodyAltColor">
      <img src={TrustedBy} alt="Trusted By" className="TrustedBy"></img>
      <img
        src={TrustedByMobile}
        alt="Trusted By Mobile"
        className="TrustedByMobile"
      ></img>
      <h2 className="PartnerSubtext">
        Our partners are helping shape the future of climate finance
      </h2>
      <Link to="PlatformLaunchPartners" className="JoinButton">
        Learn More
      </Link>
      <h1 className="InvPad">I</h1>
    </div>
    <div className="PageBody">
      <img src={Union} className="Union" alt="Union Leaf" />
      <h2 className="ClimateSubtext">
        Find out how Ethereum has learned from overcoming its own climate
        footprint and is paving the way foward for other industries
      </h2>
      <Link to="About" className="JoinButton">
        Learn More
      </Link>
    </div>
    <div className="PageBodyAltColor">
      <Link to="InThePress" className="JoinButton">
        <img src={PressRoundup} className="PressRoundup" alt="Press Roundup" />
      </Link>
      <h2 className="PressSubtext">
        See how our platform is making waves and sparking inspiration around the
        world
      </h2>
      <Link to="InThePress" className="JoinButton">
        Explore
      </Link>
      <h1 className="InvPad">I</h1>
    </div>
    <div className="PageBody">
      <img src={Hands} className="Hands" alt="Hands" />
      <h2 className="JoinSubtext">
        Join the initiative today and help create or contribute to the fair,
        responsible, and decentralized platform of the future.
      </h2>
      <Link to="JoinTheInitiative" className="JoinButton">
        Learn More
      </Link>
    </div>
  </>
);

export default Home;
