import "./JoinTheInitiative.css";
import JoinTheInitiativeBanner from "../Assets/JoinTheInitiativeBanner.webp";
import JoinTheInitiativeBannerMobile from "../Assets/JoinTheInitiativeBannerMobile.webp";
import { Helmet } from "react-helmet";
import React from "react";
// import HubspotForm from 'react-hubspot-form';

const JoinTheInitiative = () => (
  <>
    <Helmet>
      <title>Join The Initiative | Ethereum Climate Platform</title>
      <meta
        name="description"
        content="ECP invites stakeholders from the web3 space and climate technology, carbon market companies and global organizations to join the initiative."
      />
    </Helmet>
    <img
      src={JoinTheInitiativeBanner}
      alt="bannerImage"
      className="JoinTheInitiativeBanner"
    ></img>
    <img
      src={JoinTheInitiativeBannerMobile}
      alt="bannerImageMobile"
      className="JoinTheInitiativeBannerMobile"
    ></img>
    <div className="PageBody">
      &nbsp;
      <h2>
        The ECP invites stakeholders from the Web3 space, climate technology,
        and carbon market companies that are committed to decarbonization as
        well as global organizations seeking to eradicate their own historic
        carbon debt to join the initiative.
      </h2>
      {/*<div className="HubspotFormContainer">*/}
      {/*<HubspotForm*/}
      {/*          portalId='4795067'*/}
      {/*          formId='0f1d8f79-01d5-4c01-9da7-83d2bedd450f'*/}
      {/*          onSubmit={() => console.log('Submit!')}*/}
      {/*          onReady={(form) => console.log('Form ready!')}*/}
      {/*          loading={<div>Loading...</div>}*/}
      {/*      />*/}
      {/*</div>*/}
      <div className="JoinButton">
        <a href="https://share.hsforms.com/1Dx2PeQHVTAGdp4PSvt1FDw2urwb">
          Get Involved
        </a>
      </div>
      <h2>
        In addition, civil society organizations are invited to put their
        extensive experience in climate action projects at the service of the
        Platform.
      </h2>
      <h2>Media Contact: Press@EthereumClimate.com or PR@ConsenSys.net</h2>
    </div>
  </>
);

export default JoinTheInitiative;
